// Header principal
.header {
    @include flexbox(row, center, center);
    width: 100%;
    height: $header-height;
    background-color: $color-card-background;
    position: relative;
    

    .direction__icon {
        position: absolute;
        top: 17px;
        left: 16px;
        @include flexbox(row, center, center);
        width: 30px;
        height: 30px;
        color: $color-secondary;

        a {
            @include flexbox(row, center, center);

            i {
                font-size: $font-size-medium;
                color: $color-secondary;
            }
        }
    }

    .logo__container {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        margin-top: 5px;

        img {
            width: auto;
            height: 36px;
            display: block;
        }
    }
}

