// BanniÃ¨re image
.restaurant-banner {
    position: relative;
    width: 100%;
    overflow: hidden;
    z-index: 1;

    .banner-img {
        width: 100%;
        height: 275px;
        object-fit: cover;
        display: block;
    }
}

